<template>
  <section>
    <base-card>
      <canvas id="my_canvas" width="800" height="500"> </canvas>
    </base-card>
     <base-card>
      <div>
    <div class="container">
        <ul id="exampele-1">
            <li>
 <div class="name" >ID</div>
      <div class="name">MAC</div>
      <div class="name">PARENT</div>
      <div class="name">LAYER</div> 
      <div class="name">SPEED</div>
      <div class="name">STATUS</div>
      <div class="name">LAST ACTIVE</div>
            </li>
     
      </ul>
    </div>
     <div class="container">
    <ul id="exampele-1">
        <li v-for="item in getReceivedData" :key="item.id">
            <div class="name" >{{ item.id }}</div>
            <div class="name">{{ item.mac }}</div>
            <div class="name">{{ item.parent }}</div>
            <div class="name">{{ item.layer }}</div>
            <div class="name">{{ item.speed }}</div>
            <div class="name">{{ item.status }}</div>
            <div class="name">{{ item.time }}</div>
        </li>
    </ul>
    </div>
     </div>
  </base-card>
  </section>
</template>

<script>
import BaseCard from "./BaseCard.vue";

const url =  `http://localhost:4000api/posts`;
export default {
  components: {
    BaseCard
  },
  data() {
    return {
      CanvasWidth: 800,
      CanvasHeight: 500,
      CanvasJsonData: [
      ],
      DrawingObject: {
        root: {},
      },
      loading: true,
      polling: null
    };
  },
  mounted() {
    var c = document.getElementById("my_canvas");
    var ctx = c.getContext("2d");
    this.vueCanvas = ctx;
    this.loadServerData();
   
  },
  computed : {
       getReceivedData(){
           return this.$store.state.GlobalReceivedData;
       }
  },
beforeUnmount () {
	clearInterval(this.polling)
},
created () {
	this.pollData()
},
  methods: {
      pollData () {
		this.polling = setInterval(() => {
			this.loadServerData();
		}, 1000)
	},
      setReceivedData(data){
           this.$store.state.GlobalReceivedData = data;
       },
    loadServerData() {
      fetch( url)
        .then((response) => {
          if (response.ok) {
            return response.json();
          }
        })
        .then((data) => {
          this.isLoading = false;
          console.log(JSON.stringify(data));
          this.CanvasJsonData = data;
          this.setReceivedData(data);
          this.plotCanvas();
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
          this.error = "Failed to fetch data - please try again later.";
        });
    },
    plotCanvas() {
      var i = 0,
        i2 = 0,
        i3 = 0,
        i4 = 0,
        i5 = 0;
      var childCount_L2, childCount_L3, childCount_L4, childCount_L5;
      var temp, temp_root;
      const len = this.CanvasJsonData.length;
      var nodeData;
      //get the root node
      //**************************LAYER 1 */
      for (i = 0; i < len; i++) {
        nodeData = this.CanvasJsonData[i];
        if (nodeData.layer == 1) {
          this.DrawingObject.root = nodeData;
          this.DrawingObject.root["childern"] = [];
          this.DrawingObject.root["x"] = this.CanvasWidth / 2;
          this.DrawingObject.root["y"] = 50;
          this.DrawingObject.root["free_x_start"] = 0;
          this.DrawingObject.root["isParent"] = true;
          this.DrawingObject.root["free_x_end"] = this.CanvasWidth;
        }
      }

      this.DrawingObject.root = this.formatChildern(this.DrawingObject.root);
      childCount_L2 = this.DrawingObject.root.childern.length;
      for (i2 = 0; i2 < childCount_L2; i2++) {
        if (this.DrawingObject.root.childern[i2].isParent) {
          this.DrawingObject.root.childern[i2] = this.formatChildern(
            this.DrawingObject.root.childern[i2]
          );
          //Layer 3
          childCount_L3 = this.DrawingObject.root.childern[i2].childern.length;
          for (i3 = 0; i3 < childCount_L3; i3++) {
            if (this.DrawingObject.root.childern[i2].childern[i3].isParent) {
              this.DrawingObject.root.childern[i2].childern[i3] = this.formatChildern(
                this.DrawingObject.root.childern[i2].childern[i3]
              );
              //Layer 4
              childCount_L4 = this.DrawingObject.root.childern[i2].childern[i3].childern
                .length;
              for (i4 = 0; i4 < childCount_L4; i4++) {
                if (
                  this.DrawingObject.root.childern[i2].childern[i3].childern[i4].isParent
                ) {
                  this.DrawingObject.root.childern[i2].childern[i3].childern[
                    i4
                  ] = this.formatChildern(
                    this.DrawingObject.root.childern[i2].childern[i3].childern[i4]
                  );
                  //Layer 5
                  childCount_L5 = this.DrawingObject.root.childern[i2].childern[i3]
                    .childern[i4].childern.length;
                  for (i5 = 0; i5 < childCount_L5; i5++) {
                    if (
                      this.DrawingObject.root.childern[i2].childern[i3].childern[i4]
                        .childern[i5].isParent
                    ) {
                      this.DrawingObject.root.childern[i2].childern[i3].childern[
                        i4
                      ].childern[i5] = this.formatChildern(
                        this.DrawingObject.root.childern[i2].childern[i3].childern[i4]
                          .childern[i5]
                      );
                    }
                  }
                }
              }
            }
          }
        }
      }

      //console.log(JSON.stringify(this.DrawingObject));
      temp = this.DrawingObject.root;
      temp_root = this.DrawingObject.root;
      this.drawCircle(temp.x, temp.y, temp.id, temp_root.x, temp_root.y,temp.status);
      childCount_L2 = this.DrawingObject.root.childern.length;
      for (i2 = 0; i2 < childCount_L2; i2++) {
        temp = this.DrawingObject.root.childern[i2];
        temp_root = this.DrawingObject.root;
        this.drawCircle(temp.x, temp.y, temp.id, temp_root.x, temp_root.y,temp.status);
        if (this.DrawingObject.root.childern[i2].isParent) {
          //Layer 3
          childCount_L3 = this.DrawingObject.root.childern[i2].childern.length;
          for (i3 = 0; i3 < childCount_L3; i3++) {
            temp = this.DrawingObject.root.childern[i2].childern[i3];
            temp_root = this.DrawingObject.root.childern[i2];
            this.drawCircle(temp.x, temp.y, temp.id, temp_root.x, temp_root.y,temp.status);
            if (this.DrawingObject.root.childern[i2].childern[i3].isParent) {
              //Layer 4
              childCount_L4 = this.DrawingObject.root.childern[i2].childern[i3].childern
                .length;
              for (i4 = 0; i4 < childCount_L4; i4++) {
                temp = this.DrawingObject.root.childern[i2].childern[i3].childern[i4];
                temp_root = this.DrawingObject.root.childern[i2].childern[i3];
                this.drawCircle(temp.x, temp.y, temp.id, temp_root.x, temp_root.y,temp.status);

                if (
                  this.DrawingObject.root.childern[i2].childern[i3].childern[i4].isParent
                ) {
                  //Layer 5
                  childCount_L5 = this.DrawingObject.root.childern[i2].childern[i3]
                    .childern[i4].childern.length;
                  for (i5 = 0; i5 < childCount_L5; i5++) {
                    temp = this.DrawingObject.root.childern[i2].childern[i3].childern[i4]
                      .childern[i5];
                    temp_root = this.DrawingObject.root.childern[i2].childern[i3]
                      .childern[i4];
                    this.drawCircle(temp.x, temp.y, temp.id, temp_root.x, temp_root.y,temp.status);
                  }
                }
              }
            }
          }
        }
      }
    },
    formatChildern(rootObj) {
      var nodeData,
        i = 0,
        j = 0;
      const len = this.CanvasJsonData.length;
      //console.log("START = " + JSON.stringify(rootObj));
      //parse layer 2 nodes
      for (i = 0; i < len; i++) {
        nodeData = this.CanvasJsonData[i];
        if (rootObj.mac == nodeData.parent) {
          nodeData["childern"] = [];
          rootObj.childern.push(nodeData);
        }
      }
      //console.log("BETWEEN = " + JSON.stringify(rootObj));
      var childCount = rootObj.childern.length;
      var division = rootObj["free_x_end"] / (childCount + 1);
      var accessLen = rootObj["free_x_end"] / childCount;

      for (i = 0; i < childCount; i++) {
        //console.log("START NODE = " +JSON.stringify(nodeData));
        rootObj.childern[i]["x"] = division * (i + 1);
        rootObj.childern[i]["y"] = (rootObj.childern[i].layer - 1) * 100 + 50;
        rootObj.childern[i]["free_x_start"] = accessLen * i;
        rootObj.childern[i]["free_x_end"] = accessLen * (i + 1);
        var flag = false;
        for (j = 0; j < len; j++) {
          var item = this.CanvasJsonData[j];
          if (rootObj.childern[i].mac == item.parent) {
            flag = true;
            break;
          }
        }
        rootObj.childern[i]["isParent"] = flag;
        //console.log("END NODE = " +JSON.stringify(nodeData));
        //rootObj.childern[i] = nodeData;
      }
      // console.log("END = " +JSON.stringify(rootObj));
      return rootObj;
    },
    clearCanvas() {
      this.vueCanvas.clearRect(0, 0, 800, 500);
    },
    drawRect() {
      // clear canvas
      this.clearCanvas();
      this.vueCanvas.fillStyle = "red";
      this.vueCanvas.fillRect(0, 0, 100, 100);
    },
    drawCircle(x, y, name, p_x, p_y,status) {
      // draw rect
      
      if(status == "ONLINE"){
           this.drawLine(p_x, p_y, x, y);
            this.vueCanvas.fillStyle = "green";
      }else{
            this.vueCanvas.fillStyle = "red";
      }
      this.vueCanvas.beginPath();
      
      this.vueCanvas.arc(x, y, 25, 0, 2 * Math.PI);
      this.vueCanvas.fill();
      this.vueCanvas.fillStyle = "black";
      this.vueCanvas.font = "20px Georgia";
      this.vueCanvas.fillText(name, x, y, 100);
      
      
    },
    drawLine(x_start, y_start, x_end, y_end) {
      this.vueCanvas.beginPath();
      this.vueCanvas.lineWidth = 5;
      this.vueCanvas.moveTo(x_start, y_start);
      this.vueCanvas.lineTo(x_end, y_end);
      this.vueCanvas.stroke();
    },
  },
};
</script>

<style scoped>
.container{
  height: 25px;
  display: flex;
}
.name{
  display:inline-block;
  height: 20px;
  width: 200px;
  text-align: left;
  
  border: 1px solid #886a6a;
  background-color: #886a6a;
  color: white;
  flex: 1;
}
ul {
    list-style: none;
    margin: 0 0 0 0;
}
li {
    list-style: none;
     margin: 0 0 0 0;
}
</style>